.background {
    position: absolute;
    top: 0;
    margin: auto;    
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    width: 100%;
    background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("../Assets/background.jpg");
    filter: blur(7px) grayscale(50%);
}
.container {
    position: relative;
    background-image: linear-gradient(180deg, rgb(4, 34, 58) 0%, rgb(36, 109, 206) 100%);
    border-radius: 10px;
    margin: auto;
    width: 400px;
    height: 400px;
    margin-top: 20vh;
}

.top-bar {
    display: flex;
    justify-content: center;
    border-radius: 45px;
    position: relative;
    width: 90%;
    height: 10%;
    margin: auto;

}
.top-bar input {
    display: flex;
    width: 75%;
    height: 70%;
    border-radius: 13px;
    border: none;
    outline: none;
    position: relative;
    font-size: auto;
    margin-top: 5%;
}
.search-icon {
    position: relative;
    border-radius: 75%;
    width: 8%;
    height: 75%;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    margin-top: 5%;
    margin-left: 2%;
}
.search-icon img {
    width: 60%;
    height: 65%;
}

.weather-image {
    display: flex;
    margin: auto;
    margin-top: 2%;
    justify-content: center;
    height: 30%;
    width: 30%;
    font-size: 100px;
}

.weather-temp {
    display: flex;
    justify-content: center;
    margin: auto;
    color: white;
    width: 20%;
    height: 13%;
    font-size: 2em;
}

.weather-location {
    display: flex;
    justify-content: center;
    margin: auto;
    color: white;
    height: 10%;
    font-size: 1.5em;
}

.data-container {
    margin-left: 4%;
    height: 30%;
    color: white;
    display: flex;
    justify-content: center;
    justify-items: center;
}

.element {
    width: 40%;
    margin: auto;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    gap: 2%;
}

.data {
    margin: auto;
    font-size: 1.0em;
    font-weight: 400;
}

.text {
    font-size: 1em;
    font-weight: 400;
    justify-content: center;
}
.icon {
    margin-top: 5%;
}
